	export default {
		data() {
			return {
				array: [],
                index: "",
				List:[],
			}
		},
		onLoad:function(){ 
			var _self = this
			_self.getArr()
			
			
		},
		methods: {
			bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value
			},
			getArr:function(){
				var _self = this
				uni.request({ 
					url:_self.$api+"dockingManager/titleFaceQuery",
					method:"GET",
					success:function(res){
						console.log(res)
						for(var i = 0;i<res.data.length;i++){
							_self.array[i] = res.data[i].name
						}
						_self.getInfo()
					}
				})
			},
			getInfo:function(){
				var _self = this
				uni.request({ 
					url:_self.$api+"dockingManager/enterSimpleQuery",
					data:{},
					method:"GET",
					success:function(res){
						_self.List = res.data
						console.log(res.data)
					}
				})
			},
			addInfo:function(){
				var _self = this
				var myreg = /^((0\d{2,3}-\d{7,8})|(1[3456789]\d{9}))$/;
				if(_self.name == "" || _self.name==null){
					uni.showToast({
						title:"请输入联系人",
						icon:"none"
					})
					return false
				}else if(!myreg.test(_self.phone)) {
					uni.showToast({
						title:"联系电话格式不正确",
						icon:"none"
					})
					return false
				}else if(_self.index==""){
					uni.showToast({ 
						title:"请选择入驻类型",
						icon:"none"
					})
					return false
				}
				console.log(_self.array[_self.index])
				uni.request({ 
					url:_self.$api+"dockingManager/haltAdd",
					data:{name:_self.name,state:_self.array[_self.index],phone :_self.phone,optionId:uni.getStorageSync("openId")},
					method:"GET",
					success:function(res){
						console.log(res)
						if(res.data == 1){
							uni.showToast({
								title:"提交成功",
								success:function(){
									setTimeout(function(){
										uni.switchTab({
											url:"../../../pages/server2/server2"
										})
									},1000)
								}
							})
							return
						}
						if(res.data == 99){
							uni.showModal({
								title: '提示',
								content: '此操作需用户授权，是否进行授权',
								success: function (res) {
									if (res.confirm) {
										//跳转到授权页面  
										uni.navigateTo({
											url:"../../login/login"  
										})
										console.log('用户点击确定');
									} else if (res.cancel) { 
										console.log('用户点击取消');
									}
								}
							});
							return
						}
						else{
							uni.showToast({
								title:"提交失败",
								icon:"none",
								})
							return false
						}	
					}
				})
			  }
			}
		
	}
